const TopBar = ({ className, onMobileNavOpen, ...rest }) => {
  const classes = useStyles()
  const history = useHistory()

  return (
    <AppBar
      className={classes.appBar}
      elevation={0}
      style={{
        background:
          'linear-gradient(90deg, #1A1243 0%, #251261 21.08%, #1A1243 34.16%, #331960 74.98%, #1A1243 100%)',
        // backgroundColor: "#fff",
        backdropFilter: 'blur(100px)',
      }}
      {...rest}
    >
      <Toolbar className={classes.toolbar}>
        <TopBarData />
      </Toolbar>
    </AppBar>
  )
}

TopBar.propTypes = {
  className: PropTypes.string,
}
TopBar.defaultProps = {
  onMobileNavOpen: () => {},
}
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />
})
export default TopBar

export function TopBarData() {
  // const handleToggle = (name) => {
  //   setOpen1({ ...open1, [name]: !open1[name] });
  // };
  const { account } = useWeb3React()
  const anchorRef = { community: useRef(null), user: useRef(null) }
  const classes = useStyles()
  const history = useHistory()
  const [open1, setOpen1] = React.useState(false)
  const [menuButtonContent, setMenuButtonContent] = useState([])
  const [open, setOpen] = React.useState(false)
  const [anchorEl, setAnchorEl] = useState(null)
  const [anchorElNav, setAnchorElNav] = useState(null)
  const [openMenuNav, setOpenMenuNav] = useState(false)
  const user = useContext(UserContext)
  const handleDrawerOpen = () => {
    //setOpen(true);
    user.setIsDrawerOpen(true)
  }

  // const handleDrawerClose = () => {
  //   setOpen(false);
  // };

  const handleClose2 = (event, name) => {
    if (
      anchorRef[name].current &&
      anchorRef[name].current.contains(event.target)
    ) {
      return
    }

    setOpen1({ ...open1, [name]: false })
  }

  function handleListKeyDown(event, name) {
    if (event.key === 'Tab') {
      event.preventDefault()
      setOpen1({ ...open1, [name]: false })
    }
  }

  const handleCloseNav = (e) => {
    setAnchorEl(null)
    setOpenMenuNav(false)
  }

  useEffect(() => {
    if (account) {
      //setOpen(false)
      user.setIsDrawerOpen(false)
      user.setOpenNetwork(false)
    }
  }, [account])

  //   useEffect(() => {
  //     const filterFun = sections[0]?.items?.filter((data, index) => {
  //       return data?.title !== "Profile";
  //     });
  //     if (account) {
  //       setMenuButtonContent(sections[0]?.items);
  //     } else {
  //       setMenuButtonContent(filterFun);
  //     }
  //   }, [account]);

  function clickNav() {
    history.push('/')
    history.go()
  }

  const handleHorseMenu = (event, id) => {
    if (id === 'horseProfile') {
      setAnchorEl(event.currentTarget)
      console.log('HIIIIT')
    }
  }

  const handleClickAway = (e) => {
    // e.preventDefault();
    setAnchorEl(null)
  }

  const { idHorse } = useParams()
  //<Button className={urlParse(window.location.pathname,item.href) ? classes.toolbarCurr : classes.toolbar}>
  const urlParse = (path, item) => {
    // console.log(idHorse, 'urlId')
    if (item === '/') {
      if (path === '/') {
        return true
      } else {
        return false
      }
    } else if (item === '#') {
      if (path === `/horse-profile/${idHorse}`) {
        return true
      } else {
        return false
      }
    } else {
      return path.includes(item)
    }
  }

  const NavItems = [
    { route: '/', name: 'Home' },
    { route: '/horse-profile/62db1a8ec6681e00222b1c0b', name: 'Horse-Profile' },
    { route: '/mint', name: 'Mint' },
  ]

  const OpenNav = (e) => {
    setAnchorElNav(e.currentTarget)
    setOpenMenuNav(true)
  }

  const MobileNav = ({ anchorElNav }) => {
    return (
      <Menu
        className={classes.dropdown}
        style={{ top: '0' }}
        id="basic-menu"
        open={openMenuNav}
        onClose={handleCloseNav}
        anchorEl={anchorElNav}
      >
        {NavItems.map((data) => {
          return (
            <MenuItem key={data.route}>
              <a href={data.route}>{data.name}</a>
            </MenuItem>
          )
        })}
      </Menu>
    )
  }

  return (
    <>
      <Box className={classes.topBarWrap}>
        <Box onClick={() => clickNav('banner')} className={classes.loko}>
          <a className={classes.linkLogo} href="https://peculiarpugs.io/">
            <img src={Logo} alt="logo" className={classes.logo} />
          </a>
        </Box>
        {/* <Box flexGrow={1} /> */}
        <Box className={classes.desktop}>
          <Box>
            {menuButtonContent &&
              menuButtonContent.map((item, index) =>
                item.href !== '#' ? (
                  <Link
                    className={classes.navButton}
                    to={item.href}
                    key={`nav${index}`}
                  >
                    {' '}
                    <ClickAwayListener
                      mouseEvent="onMouseDown"
                      touchEvent="onTouchStart"
                      onClickAway={handleClickAway}
                    >
                      <Button
                        id="basic-button"
                        href={item.href}
                        onClick={handleHorseMenu}
                        className={
                          urlParse(window.location.pathname, item.href)
                            ? classes.toolbarCurr
                            : classes.toolbar
                        }
                      >
                        <div id={item.id}>{item.title}</div>
                      </Button>
                    </ClickAwayListener>
                  </Link>
                ) : (
                  <Link
                    className={classes.navButton}
                    to={item.href}
                    key={`nav${index}`}
                    name={item.id}
                  >
                    {' '}
                    <ClickAwayListener
                      mouseEvent="onMouseDown"
                      touchEvent="onTouchStart"
                      onClickAway={handleClickAway}
                    >
                      <Button
                        id={item.id}
                        href={item.href}
                        onClick={(event) => handleHorseMenu(event, item.id)}
                        className={
                          urlParse(window.location.pathname, item.href)
                            ? classes.toolbarCurr
                            : classes.toolbar
                        }
                      >
                        <div className={classes.horseProfile} name={item.id}>
                          {item.title}
                          <img
                            src={ChevronDown}
                            alt="arrowDown"
                            name={item.id}
                          />
                        </div>
                      </Button>
                    </ClickAwayListener>
                  </Link>
                ),
              )}
            {account ? (
              <BtnConnect
                account={account}
                onClick={() => user?.deactivateWallet()}
              />
            ) : (
              <BtnConnect
                account={account}
                onClick={() => user.setIsDrawerOpen(true)}
              />
            )}
          </Box>
        </Box>
        <Box className={classes.mobile}>
          <Box display="flex" alignitem="center">
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              className={clsx(user.isDrawerOpen && classes.hide)}
            >
              <BsWallet style={{ color: '#fff' }} />
            </IconButton>
            {/* <IconButton
              id="ham"
              color="inherit"
              aria-label="open drawer"
              onClick={OpenNav}
              className={clsx(user.isDrawerOpen && classes.hide)}
            >
              <FaBars style={{ color: "#fff" }} />
            </IconButton> */}
            <MobileNav anchorElNav={anchorElNav} />

            {/* <SwipeableTemporaryDrawer sections={menuButtonContent} /> */}
          </Box>
        </Box>
      </Box>
      <Drawer
        className={classes.drawer}
        anchor="right"
        open={user.isDrawerOpen || user.openNetwork}
        onClose={() => {
          //setOpen(false);
          user.setIsDrawerOpen(false)
          user.setOpenNetwork(false)
        }}
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div className={classes.drawerHeader}>
          <Typography variant="h4">Wallet</Typography>
          <IconButton
            onClick={() => {
              //setOpen(false);
              user.setIsDrawerOpen(false)
              user.setOpenNetwork(false)
            }}
          >
            <GrClose />
          </IconButton>
          <IconButton></IconButton>
        </div>
        <Box mb={2} p={2} display="flex" justifyContent="flex-start">
          <figure>
            <img src="/images/product1.png" alt="" />
          </figure>
          <Box>
            <Typography variant="body2">
              Connect with one of our available wallet providers.
            </Typography>
          </Box>
        </Box>
        <Box className="subTotalBox" mt={2}>
          <Box p={2}>
            <Divider />
          </Box>

          <Box mt={3} p={2} className="walletlist">
            <List style={{ padding: '10px !important' }}>
              {SUPPORTED_WALLETS?.map((item, i) => {
                return (
                  <ListItem key={i}>
                    <Button
                      className={classes.link}
                      onClick={() => {
                        window.localStorage.removeItem('walletName')
                        window.localStorage.setItem('walletName', item.name)
                        user.connectWallet(item.data)
                      }}
                    >
                      <Typography variant="h3">{item.data.name}</Typography>
                      <img src={item.data.iconName} alt="" />
                    </Button>
                  </ListItem>
                )
              })}
            </List>
          </Box>
        </Box>
      </Drawer>
    </>
  )
}
